            <h1>
              The History of Battlefield Medicine: Mobile Army Surgical Hospital
            </h1>
            <div style="font-size: 18px"><i>By Byron, Shao Wei and Oon Tong</i></div><br>
        
            The Mobile Army Surgical Hospital (MASH) saw its last deployment in Operation Iraqi Freedom back in 2003. By that point, MASH had served for roughly 50 years in the United States (US) Army. This makes it one of the longest serving units in the army’s history and is comparable to other distinguished veteran units like the 1st Cavalry Division. It’s length of service is testament to its importance and relevance in the ever changing nature of the battlefield, providing much needed care and treatment for wounded frontline soldiers. Not only were these mobile hospitals capable of saving lives during conflicts, it’s procedure and innovation influenced the delivery of critical and trauma care in civilian hospitals significantly. Although MASH has been replaced with the Combat Surgical Hospital (CSH), the legacy of MASH remains as the shining standard of what battlefield medicine should aim to be.
            <br><br>

            This website chronicles the founding of MASH and what it’s personnel faced during the Korean conflict via a popular television series, M*A*S*H and thereafter, evaluate the show’s accuracy in portraying MASH units using historical sources.

            
