          <div class="next-slide-text">
            The early days of World War II saw the establishment of a mobile medical unit by Major Vincent Marran. Although the effectiveness of his efforts were admired and encouraged by command, there were no attempts to establish formal designation. 
            <br><br>
            The idea of a Mobile Army Surgical Hospital (MASH) unit was formally conceived by Michael DeBakey. It was to be a better alternative as compared to the random individual portable surgical hospitals and field hospitals systems that was widely used during World War II. Since treatment capability and time are crucial factors for the survival of casualties, the aim was to treat casualties as soon as possible with experienced personnel closer to the front. MASH was designed with that reasoning in mind. The first point of contact of battlefield medical management for casualties was through buddy aid. Casualties were then transferred to Battalion Aid Stations for emergency stabilization surgery and triage, before finally being evacuated to MASH for more extensive treatment.
            <br><br>
            <img src="https://d3i6fh83elv35t.cloudfront.net/static/2019/02/GettyImages-985232088-1200x1073.jpg" class='body-image'>
            <div class='img-tag'>American military surgeons Richard Warren and Roger Wilcox pose beside the sign outside the 8063rd MASH (Mobile Army Surgery Hospital) camp headquarters, in South Korea, April 1952</div>
            MASH units saw its first major deployment during the Korean war. Since then, they have seen action in every major conflict the US was involved in up until 2003, after which it was replaced by the CSH. Its main mission was to function as a sixty bed surgical hospital. However, in many instances the unit had to assume additional responsibilities of an evacuation hospital without compromising operational efficiency. As MASH units were usually located close to the frontlines, personnel were in no less danger from enemy raids as compared to frontline combat units.
            <br><br>
            The term MASH was made famous in movies and the subsequent television series M*A*S*H which depicts life at the fictional 4077th. The 4077th is loosely based on the real life 8055th MASH unit. 
          </div>
