          <div class="next-slide-text">
            <h4><b>Chapter 1: Accounts of Triage and Transportation</b></h4>
            <div class='episode-tag'>
              <i>
              Season 3 Episode 12, A Rich Full Da
              <br>
              Season 4 Episode 22, Smillin’
              </i>
            </div><br>
            <img src="https://i0.wp.com/www.defensemedianetwork.com/wp-content/uploads/2013/12/H-5G-Helicopter.jpg?resize=720%2C577&ssl=1" class='body-image'>
            <div class='img-tag'>Casualty being loaded onto the helicopter</div>

            Throughout the series, triage has been a regular scene of which they show patients arriving in jeeps, buses, ambulances and by helicopter. After which, the medical staff would run out to attend to the patients and bring them into Pre-Op. By this time in history, triage would have been well polished as America had adopted it in World War I and kept improving it ever since. The hustle and bustle that they portray during these scenes gave a sense of chaos of the war with patients covered in blood and the doctors and nurses shouting out their injuries over each other.
            <br><br>
            There was an episode where the plot was about Captain Pierce narrating to his father back in the US with regards to their day to day business. The first thing he introduced was triage, to which he goes on to give a brief explanation of what it means (M*A*S*H, S03E12, 02:10). This was one of the lighter scenes that was depicted in the series. An interesting point about this episode was the introduction of foreign soldiers that were part of the coalition forces and were also receiving medical treatment from the MASH units. This episode showed a Turkish soldier that was in restraint when he was brought into the hospital (M*A*S*H, S03E12, 02:45). Throughout the episode, he was seen refusing medical treatment and wanted to get back to the battlefield despite having a broken shoulder. He was depicted as rough and wielding a knife threatening everybody who got close to him. He was also unable to communicate in English. In the memoirs of Apel, he mentioned about the Turkish soldiers and their trusty knives (Apel, 1998, p. 61). He also had an anecdote about two wounded Turkish soldiers who had wielded scimitars and refused to hand them over. They were also unable to speak English and the story was similar to the described episode in M*A*S*H (Apel, 1998, p. 121).
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/turkish.png')" class='body-image'><br>
            In another episode, there was a scene of how a wounded soldier was prepared at an aid station near the frontlines to be evacuated to the MASH unit (M*A*S*H, S04E22, 01:45). After bandaging him up, they strapped him to the side of a Bell H-13 helicopter and he was flown over to the MASH unit. After which the staff proceeded to dismount the wounded soldier from the helicopter and put him onto the ambulance down to Pre-Op. The true chaos of triage was portrayed later in the episode when there were not only the helicopters bringing in wounded but also ambulances. The doctors were seen doing triage right beside the ambulances and corpsman were setting the wounded just beside them (M*A*S*H, S04E22, 21:10).
            <br><br>
            In Apel’s book, he recounted the situation on the ground when the wounded arrived. It was also from multiple vehicular mediums and the unit would be in a frenzied state with the staff all rushing about fulfilling their assigned roles. He mentioned a great deal about how important the helicopters were to the medical effort because of the poor road conditions (Apel, 1998, p. 66). Apel explained about patients being prioritized for evacuation to the MASH units and the procedures the pilot executed to fly and land (Apel, 1998, p. 77). 
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/Screenshot (72).png')" class='body-image'><br>
            The Korean war saw many firsts. Major advancements in casualty transport and medical procedure occurred during the war. Evacuation by air was initially in the Air Force’s sphere of responsibility as they used large cargo planes to transport patients to the rear. By the start of the Korean war, helicopters or ‘air ambulance’ were introduced and favoured for its quick and relative ease of casualty transportation from the frontlines. The Bell H-13 was the main helicopter used for medical evacuation (MEDEVAC) as it was the first certified helicopter. Nicknamed the ‘Angel of Mercy’, the Bell H-13 was responsible for evacuating some 18,000 United Nations casualties (Booker & Ismail, 2005, p. 650) . However, since only two patients at a time could be transported via the skids placed outside each helicopter, this meant that the treatment that each patient received during transport was limited. Evacuation by air undoubtedly was a main factor in the significant reduction in the mortality rate of wounded soldiers during the Korean conflict as compared with previous conflicts. 
            <br><br>
            Although the idea of triage is not new during the Korean war, there was substantial modification to the concept during the conflict. Triage was instituted at battalion aid stations which were small medical units that had limited capabilities. These stations were capable of supporting up to 1,000 soldiers. Here, general medical officers and nurses evaluated a soldier’s condition and wound severity. Patients needing surgery were rapidly evacuated to the MASH by helicopter. They were further evaluated based on their hemodynamic status and injury. Injuries deemed most critical were prioritized for surgery (Booker & Ismail, 2005, p. 650 - 651). Owing to the huge casualty influx at most if not all MASH hospitals, there was a need to efficiently allocate resources to where it is needed most. This meant that patients considered to unlikely survive were managed expectantly. If patients require specialised therapy such as dialysis and neurosurgery, they were then evacuated to specialty centers further back from the front line.
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/Screenshot (80).png')" class='body-image'><br>
            Triaging at MASH units was modeled after the dictum: "Life takes precedence over limb, function over anatomical defects." 
            <br><br>
            In the course of saving lives comes with improvements in areas such as anesthesia and perioperative care. These improvements were based on the collective ground staff experience from various MASH units. The practise of resuscitating casualties with crystalloid was not formally implemented until the Vietnam War (Booker & Ismail, 2005, p. 652). Before the Vietnam War, patients whose conditions were unstable were transfused whole blood. While this was effective in managing the condition for some patients, it was not entirely foolproof. Acute renal failure occurred to 0.5% of frontline casualties and needless to say, such a condition in this rough and unforgiving setting resulted in high mortality rates of about 80% to 90% despite immediate care. To combat this, anesthesiologists started using minute amounts of narcotic for induction. Chloroform and ether were also discontinued as anesthetics as it was found to be detrimental to the patients health due to their negative inotropic effects and replacing chloroform and ether was Nitrous Oxide. Other anesthesia that was recommended for use by MASH units were Thiopental for induction and Tubocurarine and succinylcholine for rapid intubation. 
