          <div class="next-slide-text">
            <h4><b>Chapter 2: Bugging out</b></h4>
            <div class='episode-tag'>
              <i>
              Season 5 Episode 1, Bug out
              </i>
            </div><br>
            <img src="https://www.mash4077tv.com/images/misc/spotlight/097_bug_out.jpg" class='body-image'>
            <div class='img-tag'>Bugging out sequence depicted in M*A*S*H</div>

            As the name implies, the Mobile Army Surgical Hospital must be mobile and readily deployable to keep up with the constantly shifting of the frontlines during the war. While the series was mostly shot at the same location, there was an episode of which the plot revolves around a camp relocation exercise. The episode highlighted the procedures of a typical ‘bug out’ exercise that MASH units had to perform under orders from the headquarters (HQ).
            <br><br>
            The exercise begins with Colonel Potter (Commanding Officer) placing Major Burns as second-in-command while he heads off in a Bell 47 MEDEVAC helicopter to scout for a new location that is capable of housing the new MASH unit (M*A*S*H, S05E01, 15:50).
            <br><br>
            Before he leaves, he instructs Burns to pack up the unit in a specific order: (M*A*S*H, S05E01, 16:10)
            <br><br>
            <b>First Phase</b>
            <ol>
              <li>Pharmacy</li>
              <li>Lab</li>
              <li>X-Ray</li>
              <li>Pre-Op</li>
              <li>O.R.</li>
            </ol>

            <b>Second Phase</b>
            <ol start='6'>
              <li>H.Q.</li>
              <li>Mess Tent</li>
              <li>Post-Op</li>
            </ol>

            After which he takes off to the skies on the helicopter and scouts for a new place to set up the base. Inferring from the scene, it was mentioned that the new base would be located somewhere in between Chin Chon and Chuk San (M*A*S*H, S05E01, 22:20). When he gets back, the convoy is all loaded up and they start to make their way towards the new base. 
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/chopper.png')" class='body-image'><br>
            There is a scene where it shows Captain Pierce visiting a hut where it was an establishment managed by Koreans that served alcohol and food to the MASH unit. The owner, Rosie, was seen packing up the “bar” alongside her fellow Korean helpers as Pierce enters to grab a drink. It was then when she mentions that they will be moving with the MASH unit to set up her bar at the new location (M*A*S*H, S05E01, 30:30).
            <br><br>
            The mobility of MASH is typically misrepresented in popular media and in M*A*S*H. The entire narrative of the series depicts the unit at the same location throughout the war, suggesting that the location of the hospital is permanent (W. L. Whitefrom, 2005, p. 63). In reality, this is only true for short periods of time, with the longest lasting for nearly 4 months. On average, MASH units moved locations 33 times in a year, which is not surprising given the fast changing environment during war. Despite its perceived mobility, moving 100+ MASH personnel, about 400 patients and tons of logistics is no mean feat and still requires foreplanning. The key to a good ‘bug out’ or retreat was to keep in constant contact with the division they are assigned to. For example, if the division was preparing for an offensive, MASH could start stockpiling blood reserves in preparation for a large casualty influx. On the other hand, if the division was dugged in to defend their position, MASH could start packing its non essential logistics early. MASH units were typically expected to ‘bug out’ in 6 hours. 
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/Screenshot (67).png')" class='body-image'><br>
            M*A*S*H portrayed the ‘bugging out’ sequence accurately. In general, the hospital was split into two sections that took turns in accepting casualties and packing (S.C, Woodard, 2003, p. 509). The order of packing for each section is methodical. The pharmacy and laboratory was first to be packed as these two areas are not as critical in treating patients on the operating table. The X-ray tent is next to be packed and its X-ray equipment loaded up trucks. The receiving tent is collapsed once the last litter enters the operating tent and after the last operation is done, the operating tent is packed. The headquarters, cook and mess tent are loaded up next before finally, the post-op tents are taken down (S.C, Woodard, 2003, p. 509). Soldiers in this last tent would be strapped to anything that would move and taken ahead of the logistics convoy to the next hospital location. 
            <br><br>
            Other than these inaccuracies in the show, the episode captured and portrayed the procedures of a bug out as according to the memoirs. Everything from the way they dismantled the buildings and loaded up to the vehicles to the way patients were directed and organised unto the ambulances and buses was accurate. It gives the audience a good representation of how it would look like on the ground during a bug out.
            <br><br>
            The series also did well to portray the local Korean attaching themselves with the MASH units even as they relocate. These Koreans provide services and provisions to the staff of these MASH units such as food and drinks in order to earn some money to survive. Staying close to these units also kept them safe from the enemy forces. The staff also welcome them as these helps to boost the moral of the staff with some comfort during the war (Apel, 1998, p. 53).

          </div>
        </b-col>
