      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4><b>Chapter 3: MASH living conditions</b></h4><br>
            <!-- <div class='episode-tag'>
              <i>
              Season 5 Episode 1, Bug out
              </i>
            </div> -->
            <img src="http://images.publicradio.org/content/2008/05/21/20080521_mash8063rd_33.jpg" class='body-image'>
            <div class='img-tag'>An aerial view of the MASH 8063rd</div>

            The popular 1970s M*A*S*H television series was based off Richard Hooker's experience during his time as a surgeon in the Korean War. Living conditions depicted in the series appeared clean and relaxed. That depiction was far from reality and settling conditions in an actual MASH unit were far worse. MASH personnel had to endure extremely low temperature and live off the rugged Korean mountainous terrain (Booker & Ismail, 2005, p. 652). As MASH units were assigned to infantry divisions, they had to move multiple times each month to keep up with the advancing combat units. Medical staff worked around the clock to treat and care for the large casualty influx while surgeons operated continuously with little rest. In fact, it comes as no surprise that some MASH units had a monthly admission of close to over 3,000 casualties. In other words, 200 bed MASH units often had to treat 400 patients per day. The close proximity at which MASH was located, sometimes just 10 miles behind the front lines, made them vulnerable to artillery bombardment and enemy attacks. These were the conditions that MASH staff endured for the most part of 1950 and 1951 (Booker & Ismail, 2005, p. 652). By late 1953, with the war winding down, only six MASH units were left in Korea and they were re-tasked to provide treatment for prisoners of war (POW) and civilian casualties.

          </div>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4><b>Chapter 4: Women in MASH </b></h4>
            <div class='episode-tag'>
              <i>
              Season 1 Episode 21, Sticky Wicket
              </i>
            </div><br>
            <img src="https://www.mash4077tv.com/images/features/nurses_opening11.gif" class='body-image'>
            <div class='img-tag'>Opening scene of nurses in M*A*S*H</div>

            There is a brief scene in the opening credits of M*A*S*H that depicts a group of nurses running towards the helicopter pad. When the television series was aired, many viewers were confused as to why women were involved during the war. Though they were not directly involved in combat, these brave women were the closest to the fighting front in the history of America and they saved countless lives during their service (Cowdrey, 1987, p. 130). Owing to their close proximity to the fight, they had to do more than regular nurses - from duties such as blood transfusions, administering antibiotics, triaging and suturing of wounds. They even treated civilians caught in the crossfire. Sufficient to say, a MASH unit would not be complete without its nurses. Living near the front also meant that every comfort which women were thought to need had to be forsaken. They had to make do with oversized male army fatigues and boots, rough living conditions and a highly stressful workplace. However, these women were not the faint of heart for they have undergone three years of gruelling training in nursing school and graduated with the rank of lieutenant or captain (W. L. Whitefrom, 2005, p. 67).
            <br><br>
            <img src="http://www.koreanwaronline.com/arms/KWjpg/Nurse.jpg" class='body-image'>
            <div class='img-tag'>MASH nurses tending to wounded soldiers</div><br>
            Apart from external inconveniences and rough living conditions, the nurses also had to take care against the patients that they are taking care of. Wounded soldiers would often attempt to harass the nurses and this was so much an issue that sentries had to be posted each night outside the nurse's tent for protection. However, that is not to say that the nurses could not take care of themselves. Ironically, most of these nurses outrank most of the men harassing them.
            <br><br>
            <img src="http://www.koreanwaronline.com/arms/KWjpg/MashNurse.jpg" class='body-image'>
            <div class='img-tag'>A MASH personal scrub in her tent after another day's ordeal</div><br>
            By the end of the Korean War, an estimated 1,500 nurses served their tours of duty in the Korean peninsula. These nurses went on to receive 9 Legions of Merit, 173 Commendation Ribbons and 120 Bronze Star, an impressive feat that exemplifies their courageous and selfless work during the course of their service (Tortorice, 2017).
            <br><br>

            <div class='quote'>
              <img src="https://history.amedd.army.mil/ancwebsite/articles/images/quinnwithbaby.jpg" class='body-image'>
              <br>
              “The nurse must be armed to fight just as a soldier, sailor, or marine. The nurse’s weapons are knowledge and skills that can be employed to wage war on disease and injury wherever these calamities have laid low a man, woman, or child."
              <br>

              <div style='font-size:16px; padding-top: 10px;'>— Mary C. Quinn, First Lieutenant 2, 8055th MASH unit </div>
              
            </div>

          </div>
