      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4><b>Chapter 5: Satirical Show</b></h4><br>
     
            

            M*A*S*H was written and intended as satire as it’s filming took place during the Vietnam war. There were of anti-war sentiments and the jokes took a jab at many political and military leaders who were still around. The character of Hawkeye Pierce who was the leading man was seen as one who was very unregimental and liberal. His wit and remarks set the tone for the running themes of the show. The show has a clear agenda by portraying the head character in a good light, with good morals and a sense of liberalism. This is to coerce the audience to view the US participation in wars as unbecoming. It also puts down the military by making a mockery of characters who side strongly with the military and regimentation. 
            <br><br>
            <b>“Comfort” Ladies</b>
            <div class='episode-tag'>
              <i>
              Season 5 Episode 1, Bug out
              </i>
            </div><br>
            During the episode, the MASH unit was seen to have relocated to a new area to move alongside the changing frontlines. Their new compound had a preexisting building that was planned to be their new operating theater. However, Colonel Potter and Captain BJ were engaged in a heated argument with the headmistress of the building that turned out to be a brothel. They initially refused to vacate the building and when Potter mentioned they are under order from General Irving R. Hamilton, the headmistress recalls him with his nickname “Binky” (M*A*S*H, S05E01, 37:00). Later in the scene they were willing to trade their building for a wardrobe of western dresses.
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/comfortwomen.png')" class='body-image'><br>
            The writers took a jab at the military general, inferring that he too had visited these prostitutes on many occasions. They also depicted these Korean prostitutes to have been enjoying their line of work as they were not afraid when they converse with Potter or BJ in their army uniforms. The prostitutes are also seen wearing a mix of western clothing and with pieces of green army tops and hats indicating they have kept leftover pieces of clothing from the soldiers. Lastly, the head mistress spoke with American lingo throughout the conversation and the way they reacted when they saw the dresses portrayed them very “bimbotically “. 
            <br><br>
            In actual fact, the prostitutes in South Korea were very different from comfort women. After World War II,  the US government established military prostitution in South Korea. After the abolishment of the prostitution under the Japanese colonial rule, the Americans started privatizing prostitution with the US military as their main cilents. These privatised organisation were in fact homegrown and the ladies were treated better than the Japanese (Lee, 2007, p. 453). Even since then, this industry has been maintained past the Korean War with the US soldiers as their main customers. The US government supported the industry because they needed to keep the needs of their military personal in check and to also manage the health standards of the prostitutes. This is to prevent the spread of sexual transmitted dieseaes (Sang-hun, 2009).
            <br><br>

            <b>A Different Side Of The Enemy</b>
            <div class='episode-tag'>
              <i>
              Season 5 Episode 1, Bug out 
              </i>
            </div><br>

            The plot of this particular episode was about the Chinese making an offer to swap POWs because their medical facilities were incapable of treating the captured American soldiers. Once again, Captain Pierce displayed his morality when he prioritized a critically injured Chinese soldier over the other wounded American soldiers. The scene took place in a bus where Major Burns was in charge of triage but despite having a large number of incoming wounded, he chose to send in American soldiers into the operating room with minor injuries. Captain Pierce stepped out of the operation room to confront Burns when he found many critically injured Chinese on the ambulance being held back as they lost their priority for treatment thanks to Burns who reminded Pierce that the enemy is to be treated last (M*A*S*H, S03E02, 08:05).
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/chinese.png')" class='body-image'><br>
            Later in the episode, they boarded a bus filled with the Chinese POWs to meet up at Rainbow Bridge for the exchange. At the bridge, Pierce started conversing with the Dr Lin Tan, leader of the group of Chinese soldiers when he remarked that Dr Tan spoke very good English. It was then we learn that Dr Tan studied at the University of Illinois. Dr Tan mentions that US planes harassed him night and day while he tries to treat his own wounded people (M*A*S*H, S03E02, 21:15). The whole scene depicts the Chinese soldiers as civilised people willing to make a civilised gesture of swapping POWs just to save lives because of their inadequate medical facilities. This humanises the enemy, making the audience feel that they are fighting their own as Dr Lin had previously studied in the US and spoke English.
            <br><br>
            <img :src="require('D:/Downloads/Modern East Asian Nexus/fp/src/assets/enemylast.png')" class='body-image'><br>
            The only few officially recorded POW exchanges did not detail anything about well educated english speaking Chinese such as Dr Tan but from the records, the treatment of POWs are terrible. Accounts mentioned that the POW camps are grim but the allies are treated better than the South Korean prisoners (BBC, 1953). While the writers of the show wanted to portray a civilised view of the Chinese, known records show otherwise.

          </div>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4><b>6: Modern Day Battlefield Medicine</b></h4><br>
            
            Much has changed since the Korean War. The nature of modern warfare and advances in technology has resulted in MASH units to be broken into smaller and more agile medical units. These medical units are now capable of travelling to the front lines to stabilize casualties after a battle before sending them to the rear for proper treatment. This greatly improves the chances of survival of a casualty as he no longer has to spend valuable time waiting to be transported. The Singapore Armed Forces (SAF) for example has a comprehensive multi layer battlefield management system. This five step system ensures that casualties are taken care of at every possible juncture and maximises the chances of survival and recovery.
            <br><br>

            <img src="https://www.mindef.gov.sg/web/wcm/connect/pioneer/2dc73bee-0525-42b0-8b09-df6c586f304f/NOV_2017_p11_graphic.jpg?MOD=AJPERES" class='body-image'>
            <div class='img-tag'>Illustration of modern-day battlefield casualty management</div><br>
            <div class='ls-header'>1. Frontline Combat Medic</div>
            The frontline combat medic is the first point of contact for a casualty. He is trained and equipped with various medical equipments like chest seals and intravenous fluids to keep the casualty alive in the field before the casualty reaches the next point of contact. The combat medic also conducts triage in conjunction with the company medic. The company medic manages all combat medics. Each rifle platoon has a frontline combat medic attached.<br>

            <div class='ls-header'>2. Battalion Casualty Station (BCS)</div>
            The BCS is a mobile field hospital installed atop a 5 tonner truck and is equipped with Advanced Trauma Life Support. The right side of the BCS is catered for casualties who have sustained life threatening wounds or Priority 1 (P1) where there will be 2 Medical Officers on standby to perform on the spot surgery while the left side of the BCS is catered to casualties with serious wounds. It’s job is to care for all casualties that the battalion sustains after a mission. The BCS is capable of an intake of roughly 40 casualties. Each battalion has a BCS attached.<br>

            <div class='ls-header'>3. Medical Company/Mobile Surgical Team</div>
            These surgical units in the field are capable of performing damage control surgery within two hours of sustaining heavy injury. These units have an operating theatre that is modular and high dependency unit. Each brigade has a Medical Company attached.

            <div class='ls-header'>4. Combat Support Hospital (CSH)</div>
            The CSH is run by specialists from multiple disciplines and is made up of mobile containers and tents that can be scaled and are climate-controlled. Signal telecommunications allows for medical information to be exchanged between teams in the field and specialists in the CSH so that the specialists know what to expect when the casualty is received. The CSH has roughly similar functions as MASH.

            <div class='ls-header'>5. Base Hospital</div>
            Hospital staff is in charge of the casualty treatment and rehabilitation upon arriving at the base hospital.

            <div class='ls-header'>6. Land & Aeromedical Evacuation</div>
            All combat ambulances are equipped with the necessary life sustaining, communication and navigational systems onboard as it transfers the casualties between each stage of care. Casualties that are critically wounded are evacuated straight to the CSH via a Chinook CH-47 helicopter. The Chinook has the Air Mobile Life Support Unit II installed onboard and it allows flight medics to perform Intensive Care Unit-type procedures and monitoring during transportation.

          </div>
        </b-col>
      </b-row>

      <b-row>
        <b-col cols='8' offset = '2' class='next-slide-container'>
          <div class="next-slide-text">
            <h4><b>Conclusion</b></h4><br>

            M*A*S*H lasted 11 seasons across 11 years on air. While the show was meant to be a comedy with satire, there were many episodes throughout its run that featured the darker times of war. The show was also aired during the time of the Vietnam war so some of its content had to be managed because it cannot be seen as a protest to the war. It was well known that the director and writers disguised their mockery of America’s role in the war under the cover of comedy. The show had high ratings because many people could relate to it, especially those who have participated in them before. The final episode held the record for the highest percentage of homes with televisions to watch a television series. As compared to today, having a television in the house during the 1970s to 1980s was not as common. 
            <br><br>
            The portrayal of these MASH units were as authentic as possible. The production team researched extensively in order to recreate the set to mimic the actual MASH units in Korea. Most of the episodes were based on recounts from the men and women in the Korean war and the writers sought help from historians and advisors in the military to create their content. For a television series to be produced in such a way back in the days when technology was not advanced and the internet did not exist, it was a commendable effort by the production crew in attempting to recreate the scenes.
            <br><br>
            Today's military battlefield casualty management procedure has come a long way since the rudimentary personal mobile aid station in WW2. The fast evolving nature of modern warfare as well as a shift of emphasis to urban combat has highlighted the need for a small and highly mobile medical team at the ready for casualty management. The formation of the BCS and CSH addresses these needs and ensures that the casualty is cared for at every junction through the efficient allocation of medical resources. Modern day battlefield medicine has come a long way since its inception during the Korean war and is expected to improve in the years to come. 
            
          </div>
        </b-col>
      </b-row>

