            }
            }]
            }
            },
            "layout": {
            "icon-image": "pulsing-dot"
            }
          });
          map.setLayoutProperty('points', 'visibility', 'none');
        })

        
        

        this.map = map;
        this.pulsingDot = pulsingDot;
        this.map.setLayoutProperty('38th parallel', 'visibility', 'none');
        this.map.setLayoutProperty('china', 'visibility', 'none');
        this.map.setLayoutProperty('south korea', 'visibility', 'none');
        this.map.setLayoutProperty('north korea', 'visibility', 'none');
        this.map.setLayoutProperty('japan', 'visibility', 'none');
        
      },
      onScroll: function ({ el, going, direction }) {
          // this.log(`#${el.getAttribute('id')} is ${this.wrapSpan(going)} viewport, direction: ${this.wrapSpan(direction)}`)
          el.classList.toggle('active', this.$waypointMap.GOING_IN === going);
          // console.log(el)
          // if (el.classList.contains('active')){
          //   console.log("CURRENT ACTIVE!!" + el.getAttribute('id'))
        
          // console.log(going, direction);
          if (this.$waypointMap.GOING_IN === going){
            this.inView.add(el.getAttribute('id'));
            this.setImage(el.getAttribute('id'));

            this.setMap(el.getAttribute('id'));
          } else {
            this.inView.delete( el.getAttribute('id') )
          }

          if (this.inView.size == 1){
            this.setMap(this.inView.keys().next().value);
          }
      },

      setMap: function(colName){
        switch(colName){
          case 'col0':
            this.map.flyTo({
              center: [0.0712037,30.2172535],
              zoom: 1.5
              });
            this.map.setLayoutProperty('38th parallel', 'visibility', 'none');
            this.map.setLayoutProperty('china', 'visibility', 'none');
