            this.map.setLayoutProperty('china', 'visibility', 'none');
            this.map.setLayoutProperty('south korea', 'visibility', 'none');
            this.map.setLayoutProperty('north korea', 'visibility', 'none');
            this.map.setLayoutProperty('japan', 'visibility', 'none');
            this.map.setLayoutProperty('points', 'visibility', 'none');
            break;
          case 'col2':
            this.map.flyTo({
              center: [0.0712037,30.2172535],
              zoom: 1.5
              });
            break;
          case 'col3':
            console.log("Flying to korea")
            this.map.flyTo({
              center: [127.64,37.93],
