            this.map.setLayoutProperty('north korea', 'visibility', 'visible');
            this.map.setLayoutProperty('china', 'visibility', 'visible');
            this.map.setLayoutProperty('south korea', 'visibility', 'none');
            //??
            break;
          case 'col5':
            this.map.setLayoutProperty('south korea', 'visibility', 'visible');
            this.map.setLayoutProperty('points', 'visibility', 'none');
            //show divide in country
            break;
          case 'col6':
            this.map.setLayoutProperty('points', 'visibility', 'visible');
            //blinking point of MASH
            break;
          default:
            break;
        }
      },
      setImage: function(colName){
        // console.log(colName);
