      handleScroll: function(){
        
        var el = document.getElementsByClassName('active')[0];
        if (!el){
          return
        }
        var activePos = (el.offsetTop || 0)+ el.style.height;
        var distFromPos = Math.abs(activePos - window.pageYOffset- vh/2);
        // console.log(distFromPos);
        var opacity = 0.2;
        if (distFromPos > 100){
          opacity = (0.2 - distFromPos/2000) || 0;
        };

        document.getElementsByClassName("bg-images")[0].style.opacity = opacity;

      }

   }
}
